.account-list-element-edit-block {
    background-color: #DBEDF3;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid #404B69;

    .account-list-element-headline {
        color: #F73859;
        font-size: 22px;
        margin-top: 0;
        font-weight: bold;
    }

    input {
        width: 100%;
        font-size: 20px;
        
    }

    input:first-of-type {
        margin-bottom: 10px;
    }
}